// 2
// import * as  from './types';//  ==     .  == 
import * as types from './types';
import axios from '../plugins/axios';

export default {
    // :commitstate
    [types.UPDATE_CHOOSE]:({commit,state},payload) => {
        // 
        axios({
            url:'/api/home',
            params:{_page:1,_limit:10}
        }).then(
            res => {
                // console.log(res);
                // mutations:res
                commit(types.UPDATE_CHOOSE,res.data.data);
            }
        )
    },
    [types.UPDATE_WINE]:({commit,state},payload) => {
        // 
        axios({
            url:'/api/home',
            params:{_page:2,_limit:10}
        }).then(
            res => {
                // console.log(res);
                // mutations:res
                commit(types.UPDATE_WINE,res.data.data);
            }
        )
    },
    [types.UPDATE_CLASSIFY]:({commit,state},payload) => {
        // 
        axios({
            url:'/api/home',
            params:{_page:1,_limit:9}
        }).then(
            res => {
                // console.log(res);
                // mutations:res
                commit(types.UPDATE_CLASSIFY,res.data.data);
            }
        )
    },
    [types.UPDATE_DETAIL]:({commit,state},payload) => {
        // dataNameid
        let {dataName,id} = payload;
        // 
        axios({
            url:`/api/${dataName}/${id}`,
            params:{_page:1,_limit:9}
        }).then(
            res => {
                // console.log(res.data.data);
                // mutations:res
                commit(types.UPDATE_DETAIL,res.data.data[0]);
            }
        )
    },
    [types.UPDATE_USER]:({commit,state},payload) => {
        // 
        //actions->//state, localstorage->
        let {username,password} = payload;
        return axios({
            url:`/api/login`,
            method:'post',
            data:{
                username,password
            }
        }).then(
            res => {
                // console.log(res.data);
                // localStorage
                commit(types.UPDATE_USER,res.data);
                // localStorage:
                window.localStorage.setItem('shop',JSON.stringify(res.data));
                // console.log(res.data.data);
                // 
                return {err:res.data.err,msg:res.data.msg}
            });
    },

    // 
    [types.ADD_ITEM]:({commit,state},payload) => {
        // 
        let arr = [...state.buycar];
        // 
        let find = false;

        // console.log(arr);
        if(state.user.err === 0){
        // 
        arr.forEach((item,index) => {
            if(item._id == payload._id){
               item.cart_number++; 
               find = true;
            //    console.log(item);
            }
        });
        
        if(!find){
            // console.log("jjjjjjj")
            payload.cart_number = 1;
            arr.push(payload);
        }
    }
        commit(types.ADD_ITEM,arr);
    },
    [types.CHANGE_ITEM]:({commit,state},payload)=>{
        // [...state.buycar]
        // {...state}

        // console.log(types.CHANGE_ITEM,payload)
        state.buycar.forEach((item,index)=>{
            // console.log(item);
          if(item._id==payload._id){
              if(item.cart_number === 1 && payload.num === -1){
                return;
              }
              item.cart_number+=payload.num;
          }
        });
        //state.buycarstate
        commit(types.CHANGE_ITEM,state.buycar)
        // commit(types.CHANGE_ITEM,state)  
      },
      
      [types.REMOVE_ITEM]:({commit,state},payload)=>{
        
        state.buycar.forEach((item,index)=>{
          if(item._id==payload._id){
            state.buycar.splice(index,1);
          }
        });
        commit(types.REMOVE_ITEM,[...state.buycar]);
        // commit(types.REMOVE_ITEM,state.buycar);
      },
    
      [types.CLEAR_BUYCAR]:({commit,state})=>{
        commit(types.CLEAR_BUYCAR,[]);
      },
      [types.DELETE_CART]:({commit,state},payload)=>{
            // let id = payload.id;
            // 
            let arr = [...state.buycar];
            // 
            arr.forEach((item,index) => {
                if(item._id == payload._id){
                    arr.splice(index,1);
                }
            });
            commit(types.DELETE_CART,arr);
      },

}